<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>库存管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">调整成本价</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>{{title}}调整成本价</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" ref="TableDataRef" :model="TableData.info" class="demo-form-inline">
        <el-form-item label="库存组织" size="mini" prop="company_id" :rules="rules.company_id">
          <el-select clearable v-model="TableData.info.company_id" @focus="companyListInvok" placeholder="请选择" filterable @change="companyChange">
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="单据号" size="mini" v-if="title!='新增'">
          <el-input type="text" v-model="TableData.info.adjust_sn" :disabled="true" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="业务日期" size="mini" prop="business_time" :rules="rules.business_time">
          <el-date-picker v-model="TableData.info.business_time" type="date" placeholder="选择日期" value-format="yyyy-MM-dd">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="备注" size="mini">
          <el-input type="text" v-model="TableData.info.remark" maxlength="300" placeholder="请输入"></el-input>
        </el-form-item>
      </el-form>
    </div>
    <div class="add_delet">
      <el-button icon="el-icon-circle-plus-outline" type="primary" @click="addLine" class="addBtn" size="mini">增行</el-button>

      <el-button plain icon="el-icon-delete" @click="deletes" size="mini">删行</el-button>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.goods_list.data" style="width: 100%" @selection-change="handleSelectionChange" max-height="600">
        <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44" :reserve-selection="false">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_id" label="商品编号" min-width="160"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_name" label="商品名称" min-width="500">
          <template slot-scope="scope">
            <pre>{{scope.row.goods_name}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="batch" label="年周/批次" min-width="120">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="warehouse_name" label="仓库名称" width="132">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="allocation_name" label="货位名称" min-width="150">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="stocks" label="结存数量" min-width="150" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="cost_price" label="结存成本价" min-width="150" align="right">
        </el-table-column>

        <el-table-column :show-overflow-tooltip="true" property="subtotal_amount" label="结存成本总价" min-width="150" align="right">
        </el-table-column>

        <el-table-column :show-overflow-tooltip="true" property="adjust_price" label="调整后成本价" min-width="150" align="right">
          <template slot-scope="scope">
            <el-input v-model="scope.row.adjust_price" placeholder="请输入" suffix-icon="el-icon-edit-outline" @change="toPositiveNum(scope.row,'adjust_price',scope.$index)"></el-input>
          </template>
        </el-table-column>

        <el-table-column :show-overflow-tooltip="true" property="adjust_balance" label="调整后结存成本总价" min-width="150" align="right">
          <template slot-scope="scope">
            <el-input v-model="scope.row.adjust_balance" placeholder="请输入" suffix-icon="el-icon-edit-outline" @change="toPositiveNum(scope.row,'adjust_balance',scope.$index)"></el-input>
          </template>
        </el-table-column>

        <el-table-column :show-overflow-tooltip="true" label="出库成本总差价" min-width="150" align="right" :render-header="renderHeader" :formatter="returnPrice">
        </el-table-column>

        <el-table-column :show-overflow-tooltip="true" property="update_time" label="最近修改时间" min-width="170" v-if="title == '修改'">
        </el-table-column>

        <!-- <el-table-column :show-overflow-tooltip="true" property="remark" label="备注" min-width="120">
          <template slot-scope="scope">
            <el-input v-model="scope.row.remark" type="text" maxlength="300" placeholder="请输入"></el-input>
          </template>
        </el-table-column> -->

      </el-table>
    </div>

    <div slot="footer" class="dialog-footer">
      <el-button @click="goBack()">取 消</el-button>
      <el-button type="primary" @click="submit('TableDataRef')">确 定</el-button>
    </div>
    <!-- 选择商品 -->
    <goodsPopout v-if='dialogFormVisible' :incloudTop="['batch','warehouse_name','cost_price','allocation_name','stocks']"
     @close='dialogFormVisible = false' :ExcloudParams="['specification_type','pattern']"
      :searchParams='{company_id:TableData.info.company_id}' @submit="confirm" />
  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
import { getData, opthionList } from "common/utils/public_function/commonUtils";
export default {
  name: "instorageAdjustcostAdjustcostAdd",
  data() {
    return {
      authority: {},
      title: "新增",
      paramsRules: {},
      goodsAddressVisible: false, //货位列表弹框
      return_allocation_arr: {}, //当前选择的货位列表
      dialogFormVisible: false, //选择商品弹框
      addChandise: false,
      companyList: [],
      state: false, //改变cheackbox状态
      multipleSelection: [],
      TableData: {
        goods_list: { data: [] },
        info: {}
      },
      showAllocationList: true
    };
  },
  computed: {
    ...mapGetters(["adjustcostAddGet"])
  },
  methods: {
    ...mapActions(["adjustcostIndexACT"]),
    returnPrice(row, column, cellValue, index) {
      //结存成本总价-调整后结存成本总价
      if (row.adjust_price == undefined || row.adjust_balance == undefined) {
        return 0;
      } else {
        return Number(row.subtotal_amount - row.adjust_balance).toFixed(2);
      }
    },
    cost_balance(row, column, cellValue, index) {
      //结存成本价*结存数量
      row.subtotal_amount = row.cost_price * row.stocks;
      return row.subtotal_amount.toFixed(2);
    },
    renderHeader() {
      return (
        <el-tooltip class="item" effect="dark" placement="top">
          <p slot="content">
            1、出库成本总差价=结存成本总价-调整后结存成本总价
            <br />
            <br />
            2、出库成本总差价&gt;0，表示降低总成本价；
            <br />
            <br />
            3、出库成本总差价&lt;0，表示调高总成本价；
          </p>
          <span
            class="el-icon-question"
            style="cursor:pointer;font-weight:bold"
          >
            出库成本总差价
          </span>
        </el-tooltip>
      );
    },
    toPositiveNum(row, key, index) {
      if (row[key] < 0) {
        this.$message.warning("请输入大于等于0的数字");
      }
      if (row[key] < 0 || isNaN(row[key])) {
        row[key] = 0;
      }
      if (row[key] >= 1e8) {
        this.$message.warning("数值不能超过一亿");
      }
      if (key === "adjust_price") {
        this.$set(
          this.TableData.goods_list.data[index],
          "adjust_balance",
          row.adjust_price * row.stocks
        );
      } else if (key === "adjust_balance") {
        // 需求调整成本价 TAPD 1016841
        let value = 0;
        if (Number(row.stocks) != 0) {
          value = row.adjust_balance / row.stocks;
        }
        this.$set(this.TableData.goods_list.data[index], "adjust_price", value);
      }
      row.adjust_price = Number(row.adjust_price).toFixed(2);
      row.adjust_balance = Number(row.adjust_balance).toFixed(2);
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    goBack() {
      this.$router.push({
        path: "/inventory/adjustcost"
      });
      this.adjustcostIndexACT({
        goPage: this.adjustcostAddGet.goPage,
        params: this.adjustcostAddGet.params,
        pageSize: this.adjustcostAddGet.pageSize
      });
    },
    companyChange() {
      this.$set(this.TableData.goods_list, "data", []);
    },
    initData() {
      //详情数据
      this.$axMethod.infoAdjustGoods(this.TableData.info).then(({ data }) => {
        this.companyList.push({
          company_id: data.data.info.company_id,
          company_name: data.data.info.company_name
        });
        data.data.goods_list.forEach(item => {
          if (item.cost_balance) {
            item.subtotal_amount = item.cost_balance;
          }
        });
        if (data.code == "200") {
          this.$set(this, "TableData", data.data);
          this.$set(this.TableData.goods_list, "data", data.data.goods_list);
        }
      });
    },
    addLine() {
      this.dialogFormVisible = true;
    },
    opens() {
      this.state = true;
    },
    goodsdialog(val) {
      this.addChandise = true;
    },
    attribute(val) {
      this.addChandise = val;
    },
    confirm(val) {
      let flag = true;
      //批次id唯一
      let batchIdArr = this.TableData.goods_list.data.map(item => {
        return item.batch_id;
      });
      val.forEach(item => {
        if (item.subtotal_amount) {
          item.cost_balance = item.subtotal_amount;
        }
        if (!batchIdArr.includes(item.batch_id)) {
          this.TableData.goods_list.data.push(item);
        }
      });
      this.dialogFormVisible = false;
      this.state = false;
    },
    deletes() {
      if (this.multipleSelection.length) {
        let a = new Set(this.TableData.goods_list.data);
        let b = new Set(this.multipleSelection);
        let differenceABSet = new Set([...a].filter(x => !b.has(x)));
        this.TableData.goods_list.data = Array.from(differenceABSet);
      } else {
        this.$message({
          message: "请先选择要删除的商品！",
          type: "warning"
        });
      }
    },
    submit(formName) {
      let flag = true;
      if (!this.TableData.goods_list.data.length) {
        this.$message({
          message: "请先添加商品！",
          type: "warning"
        });
        return false;
      }
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.TableData.goods_list.data.forEach(item => {
            if (!item.adjust_price) {
              this.$message({
                message: "请先完善商品成本价！",
                type: "warning"
              });
              flag = false;
            }
          });
          if (!flag) return false;
          var params = this.TableData.info;
          let goods_json = this.TableData.goods_list.data.map(item => {
            return {
              // batch_id: item.batch_id,
              batch: item.batch,
              goods_id: item.goods_id,
              cost_price: item.cost_price,
              cost_balance: item.subtotal_amount,
              adjust_price: item.adjust_price,
              adjust_balance: item.adjust_balance,
              warehouse_id: item.warehouse_id,
              allocation_id: item.allocation_id,
              stocks: item.stocks
            };
          });
          if (this.TableData.info.adjust_id) {
            params.goods_json = JSON.stringify(goods_json);
          } else {
            params.goods_json = JSON.stringify(goods_json);
          }
          let api = !this.TableData.info.adjust_id
            ? "addAdjustGoods"
            : "editAdjustGoods";
          this.$axMethod[api](params).then(({ data }) => {
            if (data.code == "200") {
              this.$message({
                message: data.msg || data.data,
                type: "success"
              });
              setTimeout(() => {
                this.goBack();
              });
            }
          });
        }
      });
    }
  },
  mounted() {
    this.title = this.adjustcostAddGet.adjust_id ? "修改" : "新增";
    if (this.adjustcostAddGet.adjust_id) {
      this.$set(
        this.TableData.info,
        "adjust_id",
        this.adjustcostAddGet.adjust_id
      );
    }
    this.$set(
      this.TableData.info,
      "business_time",
      this.$publicFution.formatData(new Date())
    );
    if (this.adjustcostAddGet.adjust_id) {
      this.initData();
    } else {
      this.$set(
        this.TableData.info,
        "company_id",
        Number(localStorage.company_id)
      );
      opthionList(this, window.localStorage);
    }
  }
};
</script>

<style lang="less" scoped>
.dialog-footer {
  margin-top: 40px;
  text-align: center;
}
.dialogTips {
  margin-bottom: 10px;
}
.status-style .cell {
  display: flex;
}
</style>
